.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.main{
    background: #295772;
    width: 20%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    box-shadow: 0px 0px 12px #9FA4A7;
}

.temperature{
    height: 55%;
    width: 80%;
    background: #025BAC;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 5px solid #F5F6F7;
    font-size: 45px;
    color: white;
}

.control{
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    gap: 100px;
    width: 80%;
}

.increase,.decrease{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 110%;
    border-radius: 50%;
    border: 3px solid #F5F6F7;
    font-size: 30px;
    font-weight: bolder;
    color: white;
    cursor: pointer;
    outline: none;
}

.increase{
    background: #E0E0E0;
}

.decrease{
    background: #9FA4A7;
}

/* Responsiveness */
/* For Tab and bigger screens */
@media (max-width:1024px){
    .main{
        width: 40%;
    }

    .temperature{
        height: 40%;
    }

    .increase,.decrease{
        margin-bottom: 20px;
    }
}


/* For Mobile */
@media (max-width:600px){
    .main{
        width: 60%;
    }

    .increase,.decrease{
        height: 60%;
        width: 60%;

    }
}
